:root {
  --ff: 'Montserrat', sans-serif;
  --accent: #FFD600;
  --text: #000;
}

/* Grid */
.container::before {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: '';
  pointer-events: none;
  --grid-color: rgb(255 0 0 / 0.1);
  --grid-sides: 405px;
  --grid-gutter: 30px;
  --grid-columns: 12;
  --grid-column-width: calc( (100% - var(--grid-gutter) * var(--grid-columns)) / var(--grid-columns) );
  border: solid transparent;
  border-width: 0 calc( var(--grid-sides) - var(--grid-gutter) / 2);
  background-image: repeating-linear-gradient(to right, transparent 0, transparent calc(var(--grid-gutter) / 2), var(--grid-color) calc(var(--grid-gutter) / 2), var(--grid-color) calc(var(--grid-column-width) + var(--grid-gutter) / 2), transparent calc(var(--grid-column-width) + var(--grid-gutter) / 2), transparent calc(var(--grid-column-width) + var(--grid-gutter)));
  background-repeat: no-repeat;
}

.container,
.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MontserratRegular.woff2") format("woff2");
}

::selection {
  background-color: var(--accent);
  color: #fff;
  transition: background-color 0.3s ease-out;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

select:focus,
select:hover,
option:hover,
option:focus {
  box-shadow: none;
  border: none;
  outline: none;
  border: transparent;
}

::placeholder {
  opacity: 1;
}

button {
  border: none;
}

select[disabled],
button[disabled] {
  cursor: not-allowed;
}

label, a {
  user-select: none;
}

button:focus,
a:focus,
* [role="button"]:focus,
input:focus,
textarea:focus {
  outline: none !important;
}

input:focus,
textarea:focus {
  box-shadow: 0 0 3px #053240;
}

body {
  font-family: var(--ff);
  color: var(--text);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  background-color: #fff;
  min-width: 320px;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  position: relative;
  overflow-x: hidden;
}

body input,
body select {
  color: var(--text);
}

body input:focus,
body select:focus {
  border-color: var(--accent);
}

body input:invalid:focus,
body select:invalid:focus {
  box-shadow: none;
  border-color: var(--accent);
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]:focus,
input[type="number"]:hover {
  -moz-appearance: number-input;
}

textarea {
  resize: vertical;
}

ul {
  list-style-type: none;
}

ul, ol {
  list-style-position: inside;
  margin: 0;
  padding: 0;
}

.img-resp {
  display: block;
  max-width: 100%;
  height: auto;
}

.tal {
  text-align: left;
}

.tac {
  text-align: center;
}

.tar {
  text-align: right;
}

b, strong {
  font-weight: 700;
}

.hidden {
  display: none;
}

a {
  transition: .25s all;
}

a:hover {
  text-decoration: none;
  color: var(--accent);
}

.btn {
  cursor: pointer;
  padding: 12px 35px;
  text-align: center;
}
